import React, { useContext } from "react"
import { ThemeContext } from "providers/ThemeProvider"
import { AnchorLink } from "gatsby-plugin-anchor-links"
import {
  Container,
  Card,
  CardHead,
  CardBody,
  CardSvg,
  Button,
  ItemA,
  Centered,
} from "components/common"
import { Wrapper, Grid, Item } from "./styles"
import Card1Img from "assets/illustrations/card3_img.webp"
import Card2Img from "assets/illustrations/card2_img.webp"
import Card3Img from "assets/illustrations/card1_img.webp"
import Card1Svg from "assets/illustrations/cat_card_B.svg"
import Card3Svg from "assets/illustrations/cat_card_BE.svg"
import Card2Svg from "assets/illustrations/cat_card_cod96.svg"

const Oktatas = () => {
  const { theme } = useContext(ThemeContext)

  const description1 = (
    <ul>
      <li>
        Az elmleti alapok elsajttstl a sikeres gyakorlati vizsgig vezet
        ton szmthatsz iskolnkra.
      </li>
      <li>
        Segtnk az gyintzsben, irodnkban gyakorolhatsz az elmleti
        vizsgra, illetve igny esetn ptrkat is vehetsz.
      </li>
      <li>
        Feladatod csak annyi, hogy beiratkozz s lelkiismeretesen felkszlj az
        elmleti s gyakorlati megmrettetsre.
      </li>
    </ul>
  )

  const description2 = (
    <ul>
      <li>
        Szeretnl 750-kg feletti utnfutt, lakkocsit, lszlltt vontatni?
      </li>
      <li>
        Szerezd meg nlunk B-kategris jogostvnyod mell az igazolst,
        amellyel vezethetsz akr 4250 kg ssztmeg szerelvnyt is!
      </li>
      <li>Mindssze pr ra vezets s ksz.</li>
      <li>
        Jelentkezshez csak a szemlyid s jogostvnyod kell, orvosi s kresz
        vizsga nem szksges.
      </li>
    </ul>
  )

  const description3 = (
    <ul>
      <li>Szeretnd kihasznlni autd maximlis vontatsi kapacitst?</li>
      <li>
        BE kategris jogostvnnyal jrmtl fggen, akr 7000 kg ssztmeg
        szerelvnyt is vezethetsz!
      </li>
      <li>Elmleti s gyakorlati oktats mr Szkelyudvarhelyen is!</li>
      <li>Teljeskr gyintzs, gyakorlati oktats rugalmas idpontban.</li>
    </ul>
  )

  var nodes = [
    {
      id: 1,
      url: "url placeholder",
      name: "card1",
      background: "url(" + Card1Img + ")",
      svg: Card1Svg,
      description: description1,
      stargazers: {
        totalCount: 13,
      },
      forkCount: 40,
    },
    {
      id: 2,
      url: "url placeholder",
      name: "card2",
      background: "url(" + Card2Img + ")",
      svg: Card2Svg,
      description: description2,
      stargazers: {
        totalCount: 15,
      },
      forkCount: false,
    },
    {
      id: 3,
      url: "url placeholder",
      name: "card3",
      background: "url(" + Card3Img + ")",
      svg: Card3Svg,
      description: description3,
      stargazers: {
        totalCount: 35,
      },
      forkCount: false,
    },
  ]

  return (
    <Wrapper as={Container} id="oktatas">
      <h1>Oktats</h1>
      <Grid>
        {nodes.map(item => (
          <Item
            key={item.id}
            href={item.url}
            target="_blank"
            rel="noopener noreferrer"
            theme={theme}
            as={ItemA}
            className={`${item.id === 3 ? "lastGridItem" : ""}`}
          >
            <Card theme={theme}>
              <CardHead
                style={{
                  backgroundImage: `${item.background}`,
                  backgroundRepeat: "no-repeat",
                  backgroundSize: "contain",
                  minWidth: "100%",
                  width: "100%",
                }}
              ></CardHead>

              <CardSvg>
                <img
                  src={item.svg}
                  style={{ maxHeight: "55px", height: "55px" }}
                  alt="tanuloink"
                />
              </CardSvg>
              <CardBody>
                {item.description}
              </CardBody>
                <Centered className="card__bottom"
                style={{marginBottom: "1.5rem"}}
                >
                  <Button as={AnchorLink} to="#contact" >
                    Iratkozs
                  </Button>
                </Centered>
            </Card>
          </Item>
        ))}
      </Grid>
    </Wrapper>
  )
}

export default Oktatas
